<template>
<div class="out">
    <lay-row space="10">
         <lay-col md="12" sm="12" xs="24">
            <div class="inp1">
                <lay-textarea :rows="15" placeholder="请输入描述" :autosize="{minHeight: 100, maxHeight: 200}"  v-model="latextext"></lay-textarea>
            </div>
        </lay-col>
